import ContentCard from './ContentCard';

class LandPriceCard extends ContentCard {
  constructor() {
    super();
    this.state = { data: [] };
  }

  async componentDidMount() {
    this.fetchLandPrice();
  }

  getValues() {
    var indices = [];
    var values = [];

    var result = this.state.data;
    if (result.length === 0) return [indices, values]

    var latest_index, latest_value;
    for (var index in result.prices) {
      latest_index = result.prices[index][0];
      latest_value = result.prices[index][1].toFixed(2);

      indices.push(latest_index);
      values.push(latest_value);
    }

    this.state.latest_index = latest_index;
    this.state.latest_value = latest_value;

    return [indices, values];
  }
}

export default LandPriceCard;