import { useProxy, isProduction, isLandShown } from './Config';

class RequestData {
  code = undefined;
  handler = undefined;

  constructor(c, h) {
    this.code = c;
    this.handler = h;
  }
}

class DataModel {
  static instance = null;

  proxyUrl = 'https://cors-anywhere.herokuapp.com/';
  data = {}
  timerInterval = 10;
  timerLongInterval = 2000;
  requestedCode = [];
  fetchingCode = undefined;

  constructor() {
    console.log(useProxy ? 'enabled' : 'disabled');
    window.setTimeout(this.timerHandler, this.timerInterval, this);
  }

  isProduction() {
    return isProduction;
  }

  isLandShown() {
    return isLandShown;
  }

  init() {}

  static shared() {
    if (DataModel.instance == null) {
      DataModel.instance = new DataModel();
    }

    return this.instance;
  }

  getProxy() {
    if (useProxy) {
      return this.proxyUrl;
    }

    return "";
  }

  timerHandler(self) {
    // if something is being fetched, wait more
    if (self.fetchingCode !== undefined) {
      window.setTimeout(self.timerHandler, self.timerInterval, self);
      //console.log(self.fetchingCode, 'is being fetched, wait more');
      return;
    }

    // if queue is empty, wait more
    if (self.requestedCode[0] === undefined) {
      window.setTimeout(self.timerHandler, self.timerLongInterval, self);
      return;
    }

    var request = self.requestedCode.shift();
    var code = request.code;
    if (self.data[code] !== undefined) {
      //console.log(code, 'is already fetched, do not fetch again');
      request.handler.handleResponse(code, self.data[code]);
      window.setTimeout(self.timerHandler, self.timerInterval, self);
      return;
    }

    self.fetchingCode = code;

    self.fetchYahooFinance(request);
    window.setTimeout(self.timerHandler, self.timerInterval, self);
  }

  fetchYahooFinance(request) {
    //console.log('fetching', code);
    var code = encodeURI(request.code);

    var begin_date = (Date.parse('2000-01-01') / 1000).toString();
    var end_date = Date.now().toString();

    var targetUrl = 'https://query1.finance.yahoo.com/v8/finance/chart/' + code + '?period1=' + begin_date + '&period2=' + end_date + '&interval=1d';

    var x = new XMLHttpRequest();
    x.open('GET', this.getProxy() + targetUrl);

    var self = this;
    x.onload = x.onerror = function() {
      if (x.status === 404) return;
      code = decodeURI(code);
      var result = JSON.parse(x.responseText);
      result = result.chart.result[0];

      self.data[code] = result;
      //console.log(code, result);
      self.fetchingCode = undefined;

      request.handler.handleResponse(code, result);
    };
    x.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
    x.send('');
  }

  store(code, json) {
    console.log(code, 'is stored');
    this.data[code] = json;
  }

  load(code) {
    var result = this.data[code];
    if (result != null) {
      console.log(code, 'is loaded');
    }
    return result;
  }

  reqeustYahooFinance(code, handler) {
    this.requestedCode.push(new RequestData(code, handler));
  }
}

export default DataModel;