import React from 'react';
import DataModel from './DataModel';

class DataSource extends React.Component {
  proxyUrl = 'https://cors-anywhere.herokuapp.com/';

  constructor() {
    super();

    DataModel.shared().init();

    this.state = {
      data: [],
      latest_index: undefined,
      latest_value: 0,
      period: '1y'
    };
  }

  componentWillUnmount() {

  }

  fetchData(codes) {
    for (var index in codes) {
      var code = codes[index];

      if (code === 'chartGetHousePrice') {
        this.fetchLandPrice();
        continue;
      }

      DataModel.shared().reqeustYahooFinance(code, this);
    }
  }

  async fetchLandPrice() {
    try {
      const response = await fetch(`https://api.jonghyunho.com/v1/chartGetHousePrice`);
      const json = await response.json();
      this.setState({ data: json });
    } catch (error) {
      console.log(error);
    }
  }

  handleResponse(code, result) {
    result[code] = result;
    this.setState({ data: result });
  }

  formatDate(timestamp) {
    var d = new Date(timestamp * 1000),
    month = '' + (d.getMonth() + 1),
    day = '' + d.getDate(),
    year = d.getFullYear();

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return [year, month, day].join('.');
  }

  getPrevDate(period) {
    if (period === '3m') {
      return new Date(new Date() - 86400 * 1000 * 30 * 3) / 1000;
    } else if (period === '6m') {
      return new Date(new Date() - 86400 * 1000 * 30 * 6) / 1000;
    } else if (period === '1y') {
      return new Date(new Date() - 86400 * 1000 * 30 * 12) / 1000;
    } else if (period === '3y') {
      return new Date(new Date() - 86400 * 1000 * 30 * 12 * 3) / 1000;
    } else if (period === '5y') {
      return new Date(new Date() - 86400 * 1000 * 30 * 12 * 5) / 1000;
    } else if (period === '10y') {
      return new Date(new Date() - 86400 * 1000 * 30 * 12 * 10) / 1000;
    }
    return Date.parse('2000-01-01') / 1000;
  }
}

export function formatDate(timestamp) {
  var d = new Date(timestamp * 1000),
  month = '' + (d.getMonth() + 1),
  day = '' + d.getDate(),
  year = d.getFullYear();

  if (month.length < 2) month = '0' + month;
  if (day.length < 2) day = '0' + day;

  return [year, month, day].join('.');
}

export default DataSource;