import './App.css';

import ContentCard from './ContentCard';

class YahooFinanceCard extends ContentCard {
  async componentDidMount() {
    this.fetchData([this.props.code]);
    return;
  }

  getValues() {
    let code = this.props.code;

    var indices = [];
    var values = [];

    var result = this.state.data;
    if (result.length === 0) return [indices, values];

    result = result[code];

    if (result.meta.currency == 'KRW')
      this.units[0] = '원';
    else if (result.meta.currency == 'USD')
      this.units[0] = '달러';
    else
      this.units[0] = result.meta.currency;

    let prevDate = this.getPrevDate(this.state.period);
    var adjclose = result.indicators.adjclose[0].adjclose;

    var latest_index, latest_value;
    for (var index in result.timestamp) {
      var date = result.timestamp[index];
      if (date < prevDate) continue;

      latest_index = this.formatDate(date);
      latest_value = parseFloat(adjclose[index]).toFixed(2);

      indices.push(latest_index);
      values.push(latest_value);
    }

    this.state.latest_index = latest_index;
    this.state.latest_value = latest_value;

    return [indices,values];
  }
}

export default YahooFinanceCard;