import React from 'react';

import { isMobile, isTablet } from 'react-device-detect';
import { Button, Card, Dropdown } from 'react-bootstrap';
import { Line } from "react-chartjs-2";

import DataSource from './DataSource';

class ContentCard extends DataSource {
  period = '1y';
  palette = ['#1e6eff', '#ff7245', '#ffde00'];
  units = [];

  constructor(props) {
    super(props);

    this.handle3Month = this.handle3MonthButton.bind(this);
    this.handle6Month = this.handle6MonthButton.bind(this);
    this.handle1Year = this.handle1YearButton.bind(this);
    this.handle3Year = this.handle3YearButton.bind(this);
    this.handle5Year = this.handle5YearButton.bind(this);
    this.handle10Year = this.handle10YearButton.bind(this);
    this.handleMax = this.handleMaxButton.bind(this);
  }

  getValues() {
    var indices = [];
    var values_1 = [];
    var values_2 = [];
    for (var i = 0; i < 10; i++) {
      indices.push(i.toString());
      values_1.push((Math.random() * 50).toFixed(2));
      values_2.push((Math.random() * 50).toFixed(2));
    }

    return [indices, values_1, values_2];
  }

  static defaultProps() {
    return {
      isPeriodShown: 'true'
    }
  }

  getData() {
    var values = this.getValues();
    if (values[0].length === 0) {
      return null;
    }

    const data = {
      labels: values[0],
      datasets: [
        {
          label: '',
          data: values[1],
          fill: false,
          backgroundColor: '#1e6eff',
          borderColor: '#1e6eff',
          borderWidth: 1.5,
          pointRadius: 0,
          hitRadius: 5,
          cubicInterpolationMode: 'monotone',
          lineTension: 0.1,
          spanGaps: true
        }
      ]
    };

    return data    
  }

  render() {
    var chartFontSize = 16;
    if (isTablet);
    if (isMobile) chartFontSize = 8;

    let line_chart;
    let data = this.getData();
    if (data != null) {
      line_chart =
        <Line
          data={data}
          height={150}
          options={{
            legend: {
              display: false
            },
            scales: {
              xAxes: [{
                gridLines: {
                  drawBorder: true,
                  drawOnChartArea: true,
                  lineWidth: 0.0,
                  zeroLineWidth: 0.5
                },
                ticks: {
                  fontSize: chartFontSize,
                  autoSkip: true,
                  maxTicksLimit: 2,
                  minRotation: 0,
                  maxRotation: 0
                }
              }],
              yAxes: [{
                ticks: {
                  fontSize: chartFontSize
                },
                gridLines: {
                  drawOnChartArea: true,
                  lineWidth: 0.2,
                  zeroLineWidth: 0.5
                }
              }]
            },
            animation: {
              duration: 0
            }
        }}></Line>
    } else {
      var loading_font_size = '18px';
      if (isTablet);
      if (isMobile) loading_font_size = '11px';
      line_chart = <h6 style={{ fontSize:loading_font_size, color:'#333333' }}>데이터 로딩 중...</h6>
    }

    var style_card_title = { marginTop:'0.15rem', marginBottom:'0.5rem', marginLeft:'0.15rem'};
    if (isTablet);
    if (isMobile) style_card_title = { fontSize:'12px', marginTop:'0.15rem', marginBottom:'0.5rem', marginLeft:'0.15rem'};

    var style_period_button = { fontSize:'16px', height:'25px', lineHeight:'13px', textAlign:'center' };
    if (isTablet);
    if (isMobile) style_period_button = { fontSize:'10px', height:'20px', lineHeight:'6px', textAlign:'center' };

    let period;
    if (this.props.isPeriodShown === 'true' || this.props.isPeriodShown === undefined) {
      period =
        <>
          <div style={{ width:'100%', height:'0.5rem' }}></div>
          <div>
          <center>
          <Button variant="light" style={style_period_button} onClick={this.handle3Month}>3개월</Button>{' '}
          <Button variant="light" style={style_period_button} onClick={this.handle6Month}>6개월</Button>{' '}
          <Button variant="light" style={style_period_button} onClick={this.handle1Year}>1년</Button>{' '}
          <Button variant="light" style={style_period_button} onClick={this.handle3Year}>3년</Button>{' '}
          <Button variant="light" style={style_period_button} onClick={this.handle5Year}>5년</Button>{' '}
          <Button variant="light" style={style_period_button} onClick={this.handle10Year}>10년</Button>{' '}
          <Button variant="light" style={style_period_button} onClick={this.handleMax}>최대</Button>{' '}
          </center>
          </div>
        </>
    }

    let drop_down =
      <>
        <div width='100%' style={{ textAlign:'right' }}>
          <Dropdown alignRight style={{ style_period_button }}>
            <Dropdown.Toggle id="dropdown-basic" variant='secondary' size='sm' style={{ style_period_button }}>
              원본 데이터
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item onClick={this.handleDropDown1} style={{ style_period_button, textAlign:'center' }}>원본 데이터</Dropdown.Item>
              <Dropdown.Item onClick={this.handleDropDown2} style={{ style_period_button, textAlign:'center' }}>정규화</Dropdown.Item>
              <Dropdown.Item onClick={this.handleDropDown3} style={{ style_period_button, textAlign:'center' }}>증감율</Dropdown.Item>
              <Dropdown.Item onClick={this.handleDropDown4} style={{ style_period_button, textAlign:'center' }}>누적 증감율</Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </>;


    let card_text = [];
    if (this.state.latest_index != undefined) {
      var card_font_size = '20px';
      if (isTablet);
      if (isMobile) card_font_size = '11px';
      card_text[0] =
        <div>
          <font style={{ fontSize:card_font_size, color:this.palette[0] }}>{ this.state.latest_value } { this.units[0] }</font>
          <font color='#aaaaaa' fontSize='6px'>   ({ this.state.latest_index })</font>
        </div>;
    }

    return (
        <Card style={{ width:'96%', marginBottom:'10px', marginLeft:'2%' }}>
          <Card.Body style={{ padding:'0.75em'}}>
            <Card.Title style={style_card_title}>{this.props.title}</Card.Title>
              <div style={{ width:'100%', height:'0.45rem' }}></div>
              <div style={{ marginLeft:'0.15rem', marginBottom:'1.0rem' }}>
                { card_text[0] }
                { card_text[1] }
                { card_text[2] }
                { card_text[3] }
              </div>
              { drop_down}
              <div style={{ width:'100%', height:'0.15rem' }}></div>
            { line_chart }
            { period }
            <div style={{ width:'100%', height:'0.15rem' }}></div>
          </Card.Body>
        </Card>
    )
  }

  handle3MonthButton() { this.setState({ period: '3m' }); }
  handle6MonthButton() { this.setState({ period: '6m' }); }
  handle1YearButton() { this.setState({ period: '1y' }); }
  handle3YearButton() { this.setState({ period: '3y' }); }
  handle5YearButton() { this.setState({ period: '5y' }); }
  handle10YearButton() { this.setState({ period: '10y' }); }
  handleMaxButton() { this.setState({ period: 'Max' }); }

  handleDropDown1() { console.log(1); }
  handleDropDown2() { console.log(2); }
  handleDropDown3() { console.log(3); }
  handleDropDown4() { console.log(4); }
}

export default ContentCard;