import React from 'react';
import './App.css';

import { Navbar, Nav } from 'react-bootstrap';
import MetaTags from 'react-meta-tags';
import { isMobile, isTablet } from 'react-device-detect';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import { formatDate } from './DataSource';

import Home from './Home';
import MarketIndexComparison from './MarketIndexComparison';
import RealEstate from './RealEstate';

import { createBrowserHistory } from 'history';
import ReactGA from 'react-ga';

ReactGA.initialize('UA-115470725-2', { debug: false });

// Initialize google analytics page view tracking
const history = createBrowserHistory();
history.listen(location => {
  ReactGA.set({ page: location.pathname }); // Update the user's current page
  ReactGA.pageview(location.pathname);      // Record a pageview for the given page
});

class App extends React.Component {
  constructor(props) {
    super(props);

    console.log(':: ' + process.env.REACT_APP_TEST);
  }

  render() {
    let today = formatDate(Date.now() / 1000);
    var app_title = <h3>{today}</h3>;
    if (isTablet) ;
    else if (isMobile) app_title = <h6>{today}</h6>;

    var class_app_header = 'App-header';
    if (isTablet) ;
    else if (isMobile) class_app_header = 'Mobile-App-header';

    return (
      <div className="App">
        <MetaTags>
          <meta name='viewport' content='width=device-width, user-scalable=no'/>
          <meta name='theme-color' content='#f2f2f2'/>
        </MetaTags>
        <header className={class_app_header}>
          <Navbar expand='lg' style={{ padding:'0.5rem 2.5%' }}>
            <Navbar.Brand href="#">{app_title}</Navbar.Brand>
          </Navbar>
          <Nav activeKey="#" style={{ marginLeft:'1.5rem', marginBottom:'1.0rem' }}>
            <Nav.Item><Nav.Link href="/page">경제 지표</Nav.Link></Nav.Item>
            <Nav.Item><Nav.Link href="/page/marketindexcomparison">경제 지표 간 비교</Nav.Link></Nav.Item>
            <Nav.Item><Nav.Link href="/page/realestate">부동산</Nav.Link></Nav.Item>
          </Nav>
          <Router basename='/page' history={history}>
            <Route exact path='/' component={ Home }/>
            <Route path='/marketindexcomparison' component={ MarketIndexComparison }/>
            <Route path='/realestate' component={ RealEstate }/>
          </Router>
        </header>
      </div>
    );
  }
}

export default App;
